body {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stop-watch {
  box-sizing: border-box;
  border: 1vmin dotted #000;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stop-watch__contain-hands {
  justify-content: center;
  align-items: end;
  display: flex;
  position: absolute;
}

.stop-watch__minutes-hand {
  transform-origin: 1.5vmin 20vmin;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: round;
  animation-duration: 600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  animation-direction: normal;
  position: absolute;
}

.stop-watch__seconds-hand {
  transform-origin: .75vmin 38vmin;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: round;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: normal;
  position: absolute;
}

.stop-watch__center-dot {
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

@keyframes round {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
